.page-footer__container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  padding: 25px;
  background-color: @lighter-grey;

  @media (min-width: @tablet-width) {
    padding: 47px 50px 29px 50px;
    align-items: center;
    justify-content: space-between;
  }
}

.page-footer__logo {
  display: none;

  @media (min-width: @tablet-width) {
    display: block;
    cursor: pointer;
    padding: 0 0 15px 0;
  }
}

.page-footer__img {
  fill: @black;

  &:hover,
  &:focus {
    fill: @promo-link-right-active;
  }

  &:active {
    fill: lighten(@promo-link-right-active, 30%);
  }
}
